body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  text-align: center; }

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white; }

ul {
  padding: 0; }

li {
  list-style: none; }

.search-bar {
  border: 1px solid lightgray;
  margin: 20px;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1024px;
  width: 75vw; }
  .search-bar input {
    border-radius: 20px;
    padding: 5px;
    margin: 20px; }
  .search-bar select,
  .search-bar button {
    color: white;
    border: 1px solid lightsalmon;
    background-color: lightcoral;
    padding: 10px 20px;
    border-radius: 5px;
    margin: 20px;
    font-weight: bold; }

.music-list {
  max-width: 1024px;
  margin: 0 auto; }
  .music-list .loading {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .music-list .loading progress {
      margin: 20px 0; }
  .music-list .music-list-item {
    border: 1px solid lightgray;
    margin: 20px;
    padding: 10px; }
    .music-list .music-list-item > div {
      display: flex;
      align-items: center;
      justify-content: space-around;
      flex-wrap: wrap; }
    .music-list .music-list-item .album-image {
      width: 100px; }
    .music-list .music-list-item .description {
      text-align: left;
      margin-left: 5px; }
  .music-list .hide {
    display: none; }

.albums-list .album {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .albums-list .album .folder {
    width: 25px; }
  .albums-list .album .name {
    font-size: 12px; }

.far.fa-star,
.fas.fa-star {
  color: lightcoral; }

.album-tracks ul li {
  display: flex;
  justify-content: space-around;
  padding: 5px 0; }

.track-list-item {
  justify-content: space-between !important; }
  .track-list-item .album-image {
    width: 50px !important; }

